<template>
  <div>
    <div>
      <Header/>
    </div>
    <div class="section__padding">

    <movie-card :items="trending" :title="title" :loading="loading" :noMore="true"/>
    </div>
  </div>
</template>

<script>
import Header from '../components/Header/Header.vue'

import MovieCard from '../components/MovieCard/MovieCard.vue'

export default {
  name: 'Home',
  components: {
    MovieCard,
    Header
  },
  data(){
    return{
      loading: false,
      trending:[],
      imgApi: process.env.VUE_APP_IMG_API,
      title: this.$route.name
    }
  },
  mounted(){
    this.getData()
  },
  methods:{
    async getData(){
      try {
        this.loading =true
        let params ={
        api_key: 'a2fad2db23782cd1db5fe963718b86a4'
      }
      const res = await this.$store.dispatch('get/get/trending', params)
     this.trending = res.results;
      this.trending.forEach((el) => {
        el.poster_path = this.imgApi + el.poster_path;
      });
      this.loading = false
      } catch (error) {
        console.log(error);
        this.loading = false
      }
    }
  }
}
</script>
